.image-viewer{
    background: #0000007a;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    img{
        max-width: 100%;
        object-fit: contain;
        position: fixed;
        transform: translate(-50%, -50%);
        left: 50%;
        top: 50%;
    }
}